.checkout-container-1 {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;
  justify-content: center;
  gap: 10px;

  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;

      &:last-child {
        width: 8%;
      }
    }
  }

  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }
}

.checkout-container-2 {
  width: 55%;
  min-height: 90vh;
  display: grid;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;
  grid-template-columns: 50% 50%;
  justify-content: center;
  gap: 10px;

  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;

      &:last-child {
        width: 8%;
      }
    }
  }

  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }
}

.checkout-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  // border: 1px solid black;
  text-align: center;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;

  p {
    border-radius: 3px;
    width: 50%;
    background-color: rgb(209, 209, 209);
  }
}

.row {
  display: 'flex';
  flex-direction: 'row';
  align-items: 'center';
  justify-content: 'center';
  font-size: 20px;
  width: 10%;
  height: 5%;
  border-radius: 2px;
  transition: 0.3s;
  cursor: pointer;
}

.row:hover {
  background-color: black;
  color: white;
  transition: 0.3s;
}

.container-completed {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}