.product-card-container {
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: row;
    height: 350px;
    align-items: center;
    position: relative;
    margin-bottom: 5%;
    justify-content: center;
    margin-top: 10%;
    gap: 5%
}

.image-preview-container {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: auto;
    text-align: center;
}

.product-specifics-container {
    height: 100vh;
    align-items: center;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    text-align: center;

    div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        div {
            display: flex;
            flex-direction: row;
            gap: 10px;
            align-items: center;
            justify-content: center;
        }
    }
}