.loader-container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    background: rgba(0, 0, 0, 0.834);
    z-index: 1;
}

.spinner {
    width: 64px;
    height: 64px;
    border: 8px solid;
    border-color: #3d5af1 transparent #3d5af1 transparent;
    border-radius: 50%;
    animation: spin-anim 1.2s linear infinite;
}

@keyframes spin-anim {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

/* Checkout Status Bar */
.checkout-steps {
    display: flex;
    justify-content: space-between;
    width: 40rem;
    margin: 1rem auto;
}

.checkout-steps>div {
    border-top: 0.3rem #c0c0c0 solid;
    color: #c0c0c0;
    flex: 1 1;
    padding-top: 1rem;
}

.checkout-steps>div.active {
    border-top: 0.3rem #f0000c solid;
    color: #f0000c;
}

.countryCode {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
    ;
    font-size: 14px;
    padding: 12px 2px;
    border: 0;
    margin-right: 3px;
    outline: none;
    height: 100%;
    transition: 0.3s;
    border-radius: 3px;
    cursor: pointer;
}

.countryCode:hover {
    color: white;
    background-color: black;
    transition: 0.3s;
}